import React, { PropsWithChildren, useRef, useState } from "react";

import { animated, useTransition } from "@react-spring/web";

import { HueSelector } from "./HueSelector";
import { ModeToggle } from "./ModeToggle";
import { Nav } from "./Nav";

export function Layout({ children }: PropsWithChildren) {
  const [navOpen, setNavOpen] = useState(false);
  const [hueOpen, setHueOpen] = useState(false);

  const transition = useTransition(children, {
    initial: { opacity: 1, transform: "translateX(0px)" },
    from: { opacity: 0, transform: "translateX(8px)" },
    enter: { opacity: 1, transform: "translateX(0px)" },
    leave: { opacity: 0, transform: "translateX(-8px)" },
    config: { tension: 210, friction: 20, clamp: true },
    immediate: location.hash === "#content",
    exitBeforeEnter: location.hash !== "#content",
  });

  const closeNav = () => setNavOpen(false);

  return (
    <div className={navOpen ? "nav-open layout" : "layout"}>
      <a
        id="skip-link"
        className="skip-link"
        href="#content"
      >
        Skip to content
      </a>
      <Nav open={navOpen} setOpen={setNavOpen} />
      <div className="empty-expander">
        <div
          className="content"
          onClick={closeNav}
        >
          <div id="content" />
          {transition((styles, item) => (
            <animated.div style={styles}>{item}</animated.div>
          ))}
        </div>
        <footer
          onClick={closeNav}
        >
          <div className="footer-content">
            <div className="footer-links">
              <a
                className="footer-link"
                href="https://github.com/rclarey"
                target="_blank"
              >
                GitHub
              </a>
              <a
                className="footer-link"
                href="https://www.linkedin.com/in/rclarey/"
                target="_blank"
              >
                LinkedIn
              </a>
            </div>
            <p className="copyright">
              &copy; Russell Clarey {new Date().getFullYear()}
            </p>
          </div>
        </footer>
      </div>
      <div className="bottom-buttons">
        <HueSelector open={hueOpen} setOpen={setHueOpen} />
        <ModeToggle />
      </div>
    </div>
  );
}
