import React, {
  Dispatch,
  SetStateAction,
  useEffect,
  useRef,
  useState,
} from "react";

import { Link } from "gatsby";

export interface Props {
  open: boolean;
  setOpen: Dispatch<SetStateAction<boolean>>;
}

let lastClick = 0;
export function Nav({ open, setOpen }: Props) {
  const navRef = useRef<HTMLElement>(null);
  const [showCloseButton, setShowCloseButton] = useState(false);

  useEffect(() => {
    let to: ReturnType<typeof setTimeout> | undefined;
    if (open !== showCloseButton) {
      to = setTimeout(() => setShowCloseButton((x) => !x), 200);
    }

    return () => clearTimeout(to);
  }, [open]);

  const close = () => {
    setOpen(false);
  };

  return (
    <>
      <div className="top-buttons">
        <button
          className="icon-button"
          tabIndex={-1}
          title={showCloseButton ? "Close" : "Menu"}
          onClick={() => {
            const now = Date.now();
            if (now - lastClick > 200) {
              lastClick = now;
              setOpen((x) => !x);
            }
          }}
        >
          {showCloseButton ? "🗙" : "⛶"}
        </button>
      </div>
      <nav
        ref={navRef}
        className="main-nav"
        onFocus={() => {
          setOpen(true);
        }}
        onBlur={(e) => {
          if (!navRef.current?.contains(e.relatedTarget)) {
            setOpen(false);
          }
        }}
      >
        <div className="nav-links">
          <Link to="/" onClick={close}>
            Home
          </Link>
          {
            /*<Link to="/projects" onClick={close}>
            Projects
            </Link>*/
          }
          <Link to="/blog" onClick={close}>Blog</Link>
        </div>
      </nav>
    </>
  );
}
